.user-data {
  padding-top: 20px;
  display: flex;
  justify-content: center;
  font-family: "Montserrat", sans-serif;
}

.user-table {
  border-collapse: collapse;
  width: 75%;
}

.data-th {
  font-size: 15px;
  text-align: left;
}
.data-th > th {
  padding: 12px;
  border-bottom: 3px solid #f3f4f4;
  color: grey;
}
.data-tr > td {
  padding: 12px;
  background-color: white;
  border-bottom: 5px solid #f3f4f4;
}

.delete-button {
  background-color: #939393;
  border: none;
  color: white;
  padding: 9px 20px;
  text-decoration: none;
  cursor: pointer;
}

.delete-button:hover{
  background-color: #828282;
}

#arrow-img{
  height: 10px;
}